import React, { useState } from "react";
import { LogoPNG } from "../b64-images/LogoPNG";
import { Label } from "../general/Label";
import { InputText } from "../general/InputText";
import { Button } from "../general/Button";

export interface LoginData {
  company_code: string
  email: string
  password: string
}

interface LoginPageProps {
  forgotPasswordURL?: string
  onSubmit(data: LoginData): void
  error?: string
  loading?: boolean
}

export function LoginPage(props: LoginPageProps) {
  const [data, setData] = useState<LoginData>({
    company_code: '',
    email: '',
    password: ''
  });

  return (
    <div className={`flex font-[Poppins] h-screen`}>
      <img 
        className={`object-contain h-full`}
        src={'https://res.cloudinary.com/dtcgdxy2r/image/upload/v1745385427/Item_Left_slyxfu.png'} />
      <div className={`flex-1 flex flex-col justify-center px-[13%] gap-5 overflow-auto`}>
        <img 
          className={`h-[42px] object-contain object-left`}
          src={LogoPNG} />
        <div className={`text-[36px] font-semibold my-6`}>
          Login
        </div>
        <Label label={`Company Code`}>
          <InputText 
            value={data.company_code}
            onChange={e => setData({ ...data, company_code: e.target.value })}
            placeholder={'Input Company Code'}
            className={`w-full`}
            icon={'user'}
            onKeyUp={e => e.key === 'Enter' && props.onSubmit(data)} />
        </Label>
        <Label label={`Email`}>
          <InputText 
            value={data.email}
            onChange={e => setData({ ...data, email: e.target.value })}
            placeholder={'Input Email'}
            className={`w-full`}
            type={'email'}
            icon={'email'}
            onKeyUp={e => e.key === 'Enter' && props.onSubmit(data)} />
        </Label>
        <Label label={`Password`}>
          <InputText 
            value={data.password}
            onChange={e => setData({ ...data, password: e.target.value })}
            placeholder={'Input Password'}
            className={`w-full`}
            type={'password'}
            icon={'password'}
            onKeyUp={e => e.key === 'Enter' && props.onSubmit(data)} />
        </Label>
        <div className={`flex justify-between`}>
          { props.error && <div className={`text-[#EE1D52] text-[14px]`}>
            { props.error }
          </div>}
          { props.forgotPasswordURL && <a 
            href={props.forgotPasswordURL}
            className={`text-[#A1AFC0] text-[15px]`}>
            Forgot password?
          </a> }
        </div>
        <Button 
          loading={props.loading}
          onClick={() => props.onSubmit(data)}>
          Login
        </Button>
        <div className={`text-[#0066B9] text-[14px] text-center mt-4`}>
          @2025 - Powered by Terampil Business          
        </div>
      </div>
    </div>
  );
}
