import React from "react";

interface LabelProps {
  label?: string
  children: any
}

export function Label(props: LabelProps) {
  return (
    <div className={`flex flex-col gap-2`}>
      { props.label && <div className={`text-[14px]`}>{ props.label }</div> }
      { props.children }
    </div>
  );
}
