import React, { ButtonHTMLAttributes } from "react";

interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
  loading?: boolean
  smaller?: boolean
}

export function Button(props: ButtonProps) {
  if (props.smaller) {
    return (
      <button 
        {...props}
        className={`relative border border-px border-[#0066B9] text-white bg-[#0066B9] rounded-[10px] text-[13px] p-[8px_28px] font-[Poppins] ${props.className || ''}`}>
        { props.children }
        { props.loading && <div className={`absolute bg-white flex items-center justify-center w-full h-full left-0 top-0 bg-[#FFFD] rounded-full`}>
          <div className={'tloading'} />
        </div> }
      </button>
    );
  }
  return (
    <button
      {...props}
      className={`bg-[#0066B9] text-white rounded-full p-[10px_20px] text-[18px] font-semibold relative ${props.className || ''}`}>
      { props.children }
      { props.loading && <div className={`absolute bg-white flex items-center justify-center w-full h-full left-0 top-0 bg-[#FFFD] rounded-full`}>
        <div className={'tloading'} />
      </div> }
    </button>
  );
}
