export namespace Utility {
  export function formatNumber(n: number | string) {
    const delimiter = ',';
    const separator = '.';
    const n_normalized = String(+String(n).replace(/[^\d\.]+/g, ''));
    const [value, precision] = n_normalized.split('.');
    const value_str = _.chunk(String(value).split('').reverse(), 3).map(x => x.reverse().join('')).reverse().join(separator);
    return value_str + (precision ? (delimiter + String(precision)) : '');
  }

  export function formatRp(n: number | string) {
    return `Rp ${formatNumber(n)}`;
  }

  export function generateRandomColorHex(): string {
    return '#' + (Math.random() * 0xFFFFFF << 0).toString(16).padStart(6, '0');
  }

  export function simpleNumber(num: number): string {
    if (num >= 1_000_000_000_000) {
      return `${+(num / 1_000_000_000_000).toFixed(2)}T`;
    }
    if (num >= 1_000_000_000) {
      return `${+(num / 1_000_000_000).toFixed(2)}M`;
    }
    if (num >= 1_000_000) {
      return `${+(num / 1_000_000).toFixed(2)}K`;
    }
    return `${+(num / 1_000).toFixed(2)}`;
  }
}
