import { MailSVG } from "../b64-images/MailSVG";
import { PasswordSVG } from "../b64-images/PasswordSVG";
import { UserSVG } from "../b64-images/UserSVG";

export type InputIconType = 'user'
  | 'email'
  | 'password';

export function getIcon(type: InputIconType): string {
  switch (type) {
    case "user": return UserSVG;
    case "email": return MailSVG;
    case "password": return PasswordSVG;
  }
}
